// 
import components from './routesLazy';
let router = [
    // 
    {
        path: '/login',
        name: '',
        component: components.Login,
        hidden: true
    },
    {
        path: '/404',
        name: '404',
        component: components.UnFind,
        hidden: true
    },
    {
        path: '/',
        component: components.Home_view,
        name:'',
        iconCls: 'fa fa-address-card',//class
        children: [
            {
                path: '/PersonalInfo',
                name: '',
                component: components.PersonalInfo
            },
            {
                path: '/userManage',
                name: '',
                component: components.UserManage
            },
            {
                path: '/index',
                name: '',
                component: components.PassWord
            },

        ]

    },
    {
        path: '/',
        component: components.Home_view,
        name:'',
        iconCls: 'fa fa-address-card',//class
        children: [
            {
                path: '/groupList',
                name: '',
                component: components.groupList
            },
            {
                path: '/listEdit',
                name: '',
                component: components.groupListEdit,
                hidden:true
            },
            {
                path: '/listAdd/:id',
                name: '',
                component: components.groupListAdd,

            },
            {
                path: '/groupListAddStep',
                name: '',
                component: components.groupListAddStep,
                hidden:true
            },
            {
                path: '/packDetail',
                name: '',
                component: components.packDetail,
                hidden:true
            },
        ]

    },
    {
        path: '/',
        component: components.Home_view,
        name:'',
        iconCls: 'fa fa-address-card',//class
        children: [
            {
                path: '/goodsList',
                name: '',
                component: components.goodsList
            },
        ]

    },
    {
        path: '/',
        component: components.Home_view,
        name:'',
        iconCls: 'fa fa-plus-circle',//class
        leaf: true,//
        children: [
            {
                path: '/test',
                name: '',
                component: components.Test
            },

        ]

    },
    // 
    {
        path: '*',
        hidden: true,
        redirect: {path: '/index'}
    }
];

export default router;


